.movie {
    display: flex; 
    background-color: white;
    width: 560px;
    height: 210px;
    margin: 75px;
    position: relative;
    padding: 10px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
} 
.movie img {
    margin: 0px 5px;
    width: 120px;
    height: 160px;
    position: absolute;
    top: -21px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
.movie__data {
    margin-left: 150px;
}
.movie h3 {
    color: rgba(0,0,0,0.7);
    margin: 5px 0px;
}
.movie h5{
    color: rgba(0,0,0,0.6);
    margin: 7px 0px;
}
.movie ul {    
    display: flex;
    justify-content: flex-start;
    padding-left: 0px;
    list-style-type: none;
    color: rgba(0,0,0,0.6);
    margin: 7px 0px;
}
.movie ul > li {
    padding-right: 10px;
}
.movie span {
    color: rgba(0,0,0,0.6); 
    display: block;
    margin-top: 15px;
}