body {
    background-color: #D6D8E2; 
}
.containers {
    display: flex;
    justify-content: center;
    align-items: center; 
}

.loader { 
    font-size: 20px;
    font-weight: 600;
}

.movies {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
 
#no-mobile {
    position: absolute;
    top: 0;
    z-index: 99;
    height: 100vh;
    width: 100vw;
    background-color: #D6D8E2; 
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 32px; 
}

@media screen and (min-width:640px) {
    #no-mobile {
        display: none;
    }
}